.paginatio {
  position: relative;
  top:172px;
  left: 31.84%;
  width:30%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  cursor: pointer;
}

.paginatio a {
  position: relative;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid #E5E5E5;;
  color: #808080;
}
.paginatio a:hover {
  border-radius: 5px;
  border: 1px solid #808080;
  transition: all.3s;
}

.paginatio__link {
  position: relative;
  font-weight: bold;
}

.paginatio__link--active a {
  position: relative;
  color: #fff;
  background: #0064EB;
}

.paginatio__link--disabled a {
  position: relative;
  color: rgb(198, 197, 202);
  border: 1px solid rgb(198, 197, 202);
}

